    margin: 0;
    overflow-x: hidden;
    font-family: "Open Sans", sans-serif;
    height: 100%;
}

button {
    background: none;
    border: none;
}

body {
    background: linear-gradient(to bottom, #d8e1fe, #3464ac) no-repeat;
}

.logo {
    height: 5rem;
    width: 14rem;
}

