a:hover,
a:focus,
a:active {
    text-decoration: none;
    color: inherit;
}

.heading_main {
    font-weight: bolder;
    font-size: 1.7rem;
}

.main {
    padding: 2vh 3vw;
    /* width: 65vw; */
    /* transform: translateX(-50%); */
    margin: 0 0 0 16rem;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
}

.note {
    font-size: 1rem;
    font-weight: 300;
}

.note.top {
    position: absolute;
    right: 0.6vw;
    top: 0.1vh;
}

.note.bottom {
    position: absolute;
    right: 0.6vw;
    bottom: 0.1vh;
}

.sub-headings {
    font-weight: 400;
    font-size: 1.7rem;
}

.container {
    background: #eaf4fe;
    border-radius: 1.1rem;
    padding: 0.5vh 1vw;
    position: relative;
}

